.page--calendar #page #content {
	background-size: 40px auto;
}
.page--calendar #main .type-page.entry {
	border-bottom: 1px solid currentColor;
}
.page--calendar #main .type-page .entry-content h2:last-child {
	margin-bottom: 0.8em;
	margin-top: 4em;
}

.page--calendar #main .type-calendar-event.entry {
	margin: 1.5em auto;
	background-color: white;
	border: 1px solid currentColor;
	font-size: 0.8em;
	padding: 1.2em 1em;
}
.page--calendar #main .type-calendar-event.entry:nth-child(2) {
	margin-top: 2.5em;
}
.page--calendar #main .type-calendar-event .entry-header {
	display: grid;
	grid-gap: 2em;
	grid-template-columns: min-content 1fr;
	justify-items: center;
	margin: 0;
	padding-top: 0;
	padding-bottom: 1.5em;
	border-bottom: 2px solid #f6f4eb;
}
.page--calendar #main .type-calendar-event .event__date {
	text-transform: uppercase;
	align-self: center;
	text-align: center;
}
.page--calendar #main .type-calendar-event .event__date > span {
	display: block;
}
.page--calendar #main .type-calendar-event .event__date > span:nth-child(2) {
	font-weight: bold;
	font-size: 2em;
}
.page--calendar #main .type-calendar-event .event__image {
	max-width: 150px;
	height: 150px;
	object-fit: contain;
}
.page--calendar #main .type-calendar-event .entry-title {
	line-height: 1.4;
}
.page--calendar #main .type-calendar-event .entry-title::before,
.page--calendar #main .type-calendar-event .entry-title::after,
.page--calendar #main .type-calendar-event .event__info {
	display: none;
	content: '';
}
@media (min-width: 480px) {
	.page--calendar #main .type-calendar-event .entry-header {
		grid-template-columns: auto 150px 1fr;
		align-items: center;
		padding-bottom: 1.2em;
	}
	.page--calendar #main .type-calendar-event .entry-content {
		margin-top: 1.8em;
		margin-bottom: 1.2em;
		max-width: none;
		width: auto;
	}
	.page--calendar #main .type-calendar-event .entry-title {
		text-align: left;
		margin-bottom: 0.5em;
	}
	.page--calendar #main .type-calendar-event .event__info {
		display: block;
		justify-self: left;
	}
}
@media (min-width: 960px) {
	.page--calendar #main .type-calendar-event .entry-header,
	.page--calendar #main .type-calendar-event .entry-content {
		padding-left: calc((100vw - 960px) / 2);
		padding-right: calc((100vw - 960px) / 2);
	}
}
@media (min-width: 1140px) {
	.page--calendar #main .type-calendar-event.entry {
		max-width: 1140px;
	}
	.page--calendar #main .type-calendar-event .entry-header,
	.page--calendar #main .type-calendar-event .entry-content {
		padding-left: calc((1140px - 960px) / 2);
		padding-right: calc((1140px - 960px) / 2);
	}
}
.page--calendar #main .type-calendar-event .event__info p {
	margin: 0.4em 0;
}
.page--calendar #main .type-calendar-event .event__info .event__title {
	margin-top: 0;
}
.page--calendar #main .type-calendar-event .entry-content .event__time {
	text-align: center;
}
.page--calendar #main .type-calendar-event .entry-content .event__title {
	margin-bottom: 0.7em;
}
.page--calendar #main .type-calendar-event .entry-content .event__time {
	margin: 0;
}
.page--calendar #main .type-calendar-event .event__desc {
	margin: 0;
	line-height: 1.5;
}
